/* HEXAGON DESIGN code taken from and edited: https://www.codesmite.com/article/how-to-create-pure-css-hexagonal-grids */

.TEAMPICTURE {
    #grid {
        position: relative;
        width: 80%; /* Scales original sizes */
        margin: 0 auto;
        padding: 0 0 5% 0; /* Padding under Grid component (under last row of hexagons) */
    }

    .clear:after {
        content: "";
        display: block;
        clear: both;
    }

    /* Parallelogram */
    #grid li {
        list-style-type: none;
        position: relative;
        float: left;
        width: 14.85714285714286%; /* Directly related to original hexagon size */
        padding: 0% 0% 17% 0%; /* Directly related to original hexagon size */
        -o-transform: rotate(-60deg) skewY(30deg);
        -moz-transform: rotate(-60deg) skewY(30deg);
        -webkit-transform: rotate(-60deg) skewY(30deg);
        -ms-transform: rotate(-60deg) skewY(30deg);
        transform: rotate(-60deg) skewY(30deg);
        background: #fd005f;
        overflow: hidden;
        visibility: hidden;
    }

    #grid li .hexagon {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #fdbf00;
        -o-transform: skewY(-30deg) rotate(60deg);
        -moz-transform: skewY(-30deg) rotate(60deg);
        -webkit-transform: skewY(-30deg) rotate(60deg);
        -ms-transform: skewY(-30deg) rotate(60deg);
        transform: skewY(-30deg) rotate(60deg);
        overflow: hidden;
    }

    /* Makes all children of li visable */
    #grid li * {
        visibility: visible;
    }

    /* Makes every 2 child have padding on left and right of itself */
    #grid li:nth-child(2n + 2) {
        margin: 0 1%;
    }

    /* Manually push first row to center */
    #grid li:nth-child(1) {
        margin-left: 8.6%;
    }

    /* Translates each child in the second row to the right a little bit, and moves them down. */
    #grid li:nth-child(6),
    #grid li:nth-child(7),
    #grid li:nth-child(8),
    #grid li:nth-child(9) {
        margin-top: -3.35%;
        -o-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        -moz-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        -webkit-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        -ms-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        transform: translateX(50%) rotate(-60deg) skewY(30deg);
    }

    /* 6th child (2nd row): Move into interlocking position, which moves the rest of the children in the row. */
    #grid li:nth-child(6) {
        margin-left: 9%;
    }

    /* Forces 10th child to go to the next row. */
    #grid li:nth-child(9) {
        margin-right: 20%;
    }

    /* Translates each child in the third row down */
    #grid li:nth-child(10),
    #grid li:nth-child(11),
    #grid li:nth-child(12),
    #grid li:nth-child(13),
    #grid li:nth-child(14) {
        margin-top: -3.35%;
        -o-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        -moz-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        -webkit-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        -ms-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        transform: translateX(50%) rotate(-60deg) skewY(30deg);
    }

    /* Forces 15th child to go to the next row. */
    #grid li:nth-child(14) {
        margin-right: 20%;
    }

    /* Manually push fouth row to center */
    #grid li:nth-child(15) {
        margin-left: 24.7%;
    }

    /* Translates each child in the fourth row down */
    #grid li:nth-child(15),
    #grid li:nth-child(16) {
        margin-top: -3.35%;
        -o-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        -moz-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        -webkit-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        -ms-transform: translateX(50%) rotate(-60deg) skewY(30deg);
        transform: translateX(50%) rotate(-60deg) skewY(30deg);
    }




    /* Medium-Small devices (phones, portrait tablets, and large phones, 850px and lower) */
    @media only screen and (max-width: 850px) {
        #grid {
            position: relative;
            width: 100%; /* Scales original sizes */
            margin: 0 auto;
            padding: 0 0 5% 0; /* Padding under Grid component (under last row of hexagons) */
        }

        
    }

    .Header {
        padding: 3% 0% 2% 0%;
        text-align: center;

        h1 {
            color: black;
            font-weight: bold;
        }

        /* Media Queries taken from and edited: https://www.w3schools.com/css/css_rwd_mediaqueries.asp */

        /* Small devices (iphones, 350px and up) */
        @media only screen and (min-width: 350px) {
            font-size: 25px;
        }

        /* Small devices (phones, portrait tablets, and large phones, 600px and up) */
        @media only screen and (min-width: 600px) {
            font-size: 25px;
        }

        /* Medium devices (landscape tablets, 768px and up) */
        @media only screen and (min-width: 768px) {
            font-size: 25px;
        }

        /* Large devices (laptops/desktops, 992px and up) */
        @media only screen and (min-width: 992px) {
            font-size: 35px;
        }

        /* Extra large devices (large laptops and desktops, 1200px and up) */
        @media only screen and (min-width: 1200px) {
            font-size: 40px;
        }
    }

    .CenterHexagons {
        margin-left: 5.5%;
    }

    .break-line {
        border: 0.5rem solid #ffb181;
        opacity: 50%;
        border-width: .5rem;
        border-radius: 25px;
        margin: 0 auto;
        // box-shadow: -6px -6px 16px #fff,
        // 6px 6px 16px #696865;

        /* Smallest devices (iphones, 350px and lower) */
        @media only screen and (max-width: 350px) {
            width: 30%;
        }

        /* Small devices (iphones, 350px and up) */
        @media only screen and (min-width: 350px) {
            width: 30%;
        }

        /* Medium devices (landscape tablets, 768px and up) */
        @media only screen and (min-width: 768px) {
            width: 25%
        }

        /* Large devices (laptops/desktops, 992px and up) */
        @media only screen and (min-width: 992px) {
            width: 20%
        }

         /* Extra large devices (large laptops and desktops, 1200px and up) */
         @media only screen and (min-width: 1200px) {
            width: 20%;
        }
    }
}
