.entire-container {
    background-color: #272831;
    text-align: center;
    h1 {
        color: white;
        font-size: 40px;
        padding: 6%;
        font-weight: bold;
        text-align: center;

        @media only screen and (max-width: 900px) {
            font-size: 30px;
        }

        @media only screen and (max-width: 600px) {
            font-size: 25px;
        }
    }
    .flex-container {
        color: #c1c0cd;
        display: inline-flex;
        justify-content: center;
        text-align: center;
        h2 {
            font-size: 32px;
            font-weight: normal;
            text-shadow: 0px 0px 10px #f65331, 0px 0px 10px #ff785a;
        }
        .hackathon-tracks {
            width: 300px;
            margin: auto;
        }
        p {
            margin: auto;
            max-width: 80%;
            font-size: 18px;
        }
        .trackone {
            padding: 0% 0% 0% 3%;
        }
        .tracktwo {
            padding: 8% 0% 0% 3%;
        }
        .trackthree {
            padding: 0% 0% 0% 3%;
        }

        /* Everything under 900px width - responsiveness */
        @media only screen and (max-width: 900px) {
            display: inline;
            h2 {
                font-size: 25px;
            }
            .hackathon-tracks {
                width: 150px;
                margin: auto;
                padding: 3% 0;
            }
            p {
                margin: auto;
                max-width: 80%;
                padding: 3% 0;
                font-size: 14px;
            }

            .trackone {
                padding: 3% 1% 0% 1%;
            }
            .tracktwo {
                padding: 5% 1% 0% 1%;
            }
            .trackthree {
                padding: 5% 1% 0% 1%;
            }
        }

        @media only screen and (max-width: 600px) {
            display: inline;
            h2 {
                font-size: 18px;
            }
            .hackathon-tracks {
                width: 120px;
                margin: auto;
                padding: 3% 0;
            }
            p {
                margin: auto;
                max-width: 80%;
                padding: 3% 0;
                font-size: 12px;
            }

            .trackone {
                padding: 3% 1% 0% 1%;
            }
            .tracktwo {
                padding: 5% 1% 0% 1%;
            }
            .trackthree {
                padding: 5% 1% 0% 1%;
            }
        }
    }
}
